/* About Section with Creative Color Palette and Animations */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");

/* Header Section */
.about-section {
  padding: 80px 20px;
  background: var(--light-gray);
  text-align: center;
  font-family: "Poppins", sans-serif;
}

.about-section h2 {
  font-size: 48px;
  font-weight: 700;
  background: linear-gradient(135deg, var(--primary-red), #cc0000);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-transform: uppercase;
  letter-spacing: 3px;
  animation: fadeInUp 1s ease-out;
  margin: 0;
}

/* Full Opacity Image Section */
.about-image-section {
  padding: 0;
  background: var(--light-gray);
}

.about-image-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.about-full-image {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

/* Text Content Section */
.about-text-section {
  padding: 80px 20px;
  background: var(--light-gray);
  position: relative;
  font-family: "Poppins", sans-serif;
}

.about-container {
  max-width: 1100px; /* slightly wider */
  margin: 0 auto;
  position: relative;
  z-index: 2;
  animation: slideUp 1.2s ease-out;
}

.about-container h2 {
  font-size: 42px;
  font-weight: 700;
  margin-bottom: 25px;
  background: linear-gradient(135deg, var(--primary-red), #cc0000);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-transform: uppercase;
  letter-spacing: 3px;
  animation: fadeInUp 1s ease-out;
  text-align: center;
}

.about-container h3 {
  font-size: 28px;
  font-weight: 600;
  margin: 35px 0 20px 0;
  color: var(--dark-gray);
  text-align: center;
  animation: fadeInUp 1.2s ease-out;
}

.about-container ul,
.about-container p {
  background-color: white;
  padding: 40px; /* more breathing space */
  border-radius: 20px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.08); /* softer shadow */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  margin-bottom: 30px;
  font-size: 18px;
  color: var(--dark-gray);
  line-height: 1.9;
}

.about-container p {
  text-align: justify;
}

.about-container ul:hover,
.about-container p:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px var(--shadow-red);
}

.about-container li {
  font-size: 18px;
  color: var(--dark-gray);
  line-height: 1.9;
  margin-bottom: 12px;
  padding-left: 25px;
  position: relative;
}

.about-container li:before {
  content: "";
  color: var(--primary-red);
  font-weight: bold;
  position: absolute;
  left: 0;
  font-size: 22px;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}


/* Floating Animation */
@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-18px);
  }
  100% {
    transform: translateY(0px);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .about-section {
    padding: 60px 20px;
  }

  .about-section h2 {
    font-size: 36px;
  }

  .about-text-section {
    padding: 60px 20px;
  }

  .about-image-container {
    max-width: 100%;
  }

  .about-full-image {
    width: auto;
    max-width: 100%;
    height: auto;
    object-fit: contain;
  }

  .about-container h2 {
    font-size: 32px;
  }

  .about-container h3 {
    font-size: 24px;
  }

  .about-container p,
  .about-container ul {
    padding: 25px;
    font-size: 16px;
  }
}
